@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Pretendard-ExtraBold';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-ExtraBold.woff') format('woff');
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: 'TAEBAEKfont';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2310@1.0/TAEBAEKfont.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Cafe24ClassicType-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2210-2@1.0/Cafe24ClassicType-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

/* --------------------------------------- */

:root {
  --font-pretendard-r: 'Pretendard-Regular';
  --font-pretendard-eb: 'Pretendard-ExtraBold';
  --font-title-1: 'TAEBAEKfont';
  --font-title-2: 'Cafe24ClassicType-Regular';

  --color-main-1: #F2E205;
  --color-main-1-1: #fff243;
  --color-main-2: #262624;
  --color-main-3: #F26241;
  --color-main-4: #F25244;
  --color-main-5: #F2F2F2;
  --color-main-6: #f4876f;

  --color-para-1: #262624;
  --color-para-light-1: #F2F2F2;
  --color-bg-1: rgb(249, 249, 249);

  --color-main-high-1: #fffeec;
  --color-main-high-2: #ffeeea;
  --color-main-high-3: #fffddf;

  --color-main-win-1: rgba(242, 226, 5, 0.5);
  --color-main-win-2: rgba(0, 0, 0, 0.3);
  --color-main-win-3: rgba(255, 228, 221, 0.8);
  --color-header-win-1: rgba(255, 255, 255, 0.5);
  
  --color-main-shadow-1: rgba(242, 226, 5, 0.1);
  --color-main-shadow-2: rgba(242, 98, 65, 0.05);
  --color-main-shadow-3: rgba(242, 98, 65, 0.2);
}

body {
  overflow-x: hidden;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;

  font-family: var(--font-pretendard-r);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* 애니메이션 */

.down-PopUp {
    animation: down-PopUp 0.5s forwards;
}

.sleep {
    animation: sleep 0.5s forwards;
}

@keyframes down-PopUp {
    0% {
        translate: 0 -20px;
        opacity: 0;
    }

    100% {
        translate: 0;
        opacity: 1;
    }
}

@keyframes sleep {
    0% {
        opacity: 1;
        scale: 1;
    }

    100% {
        opacity: 0;
        scale: 0.9;
    }
}

.heartMoving {
  animation: heartMoving 1s linear forwards;
}

@keyframes heartMoving {
  0% {
    scale: 1;
    color: white;
  }

  10% {
    scale: 1.4;
    color: var(--color-main-4);
  }

  50% {
    scale: 1.05;
    color: white;
  }

  70% {
    scale: 1;
    color: white;
  }

  100% {
    scale: 1;
    color: white;
  }
}
